.swiper-container {
  width: 100%;
  height: 85vh;
  border-radius: 10px;
}
.swiper-pagination span {
  height: 15px;
  width: 15px;
  background: #37a828;
  border: 1px solid #000;
}
.swiper-button-prev::after {
  color: #37a828;
}
.swiper-button-next::after {
  color: #37a828;
}
.swiper-button-prev:hover::after {
  padding: 0 10px;
  background: rgba(138, 212, 157, 0.6);
  border-radius: 10%;
  transition: 0.2s;
  color: #fff;
  border: 1px solid #000;
}
.swiper-button-next:hover::after {
  padding: 0 10px;
  background: rgba(138, 212, 157, 0.6);
  border-radius: 10%;
  transition: 0.2s;
  color: #fff;
  border: 1px solid #000;
}
